<template>
    <div>
        <div id="navigation">
            <div id="nav-contents">
                <router-link to="/" id="logo">Quotysco</router-link>

                <div id="right">
                    <img v-if="$store.state.auth.loggedIn" id="profile-pic" src="https://cdn.interaapps.de/service/accounts/images/users/53_1393079f1b63bdc89b414e3e247925de89126637fd0fca24fa448a1c0b8fade5849d518fc3dc6b67645bced3da502b4a80327493ebc074de12b3b5b6546d5fc3.png" alt="">
                    <a v-else id="login-button" @click="login">LOGIN</a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import IAOAuth2 from '../IAOauth'
export default {
    methods: {
        login(){
            new IAOAuth2(process.env.VUE_APP_OAUTH_CLIENT_ID)
                .addScope("user.name:read")
                .addScope("user.profilepicture:read")
                .addScope("user.fullname:read")
                .addScope("user.mail:read")
                .openInNewWindow("/logging_in.html")
                    .then(res => {
                        this.api.get("/authorization/oauth2/interaapps/callback", {
                            code: res.code,
                            popup: "true"
                        })
                            .then(res=>res.json())
                            .then(res => {
                                localStorage["session"] = res.session
                                this.api.setApiKey("Bearer "+res.session)
                                this.api.getUser()
                                    .then(res => {
                                        this.$store.state.auth.loggedIn = true
                                        this.$store.state.auth.user = res
                                    })
                            })
                    })
        }
    }   
}
</script>

